@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800&display=swap);
body {
	margin: 0px;
	padding: 0px;
	display: flex;
	width: 100%;
	justify-content: center;
	align-items: center;
	box-sizing: border-box;
	background-image: url(/react-todo/static/media/bg.eea28404.jpg);
	font-family: 'Poppins', sans-serif;
}

.box {
	padding: 0px 50px;
	margin: 70px 0px;
	min-height: 550px;

	/* border: 3px solid rgb(241, 190, 190); */
	display: flex;
	align-items: center;
	flex-direction: column;
	border-radius: 30px;
	background-color: bisque;
	box-shadow: 3px 3px 10px rgb(128, 89, 89);
}

.title {
	color: rgb(231, 85, 85);
	font-weight: 800;
}

.input {
	outline: none;
	background-color: bisque;
	border-left: none;
	border-top: none;
	border-right: none;
	border-bottom: 3px solid rgb(247, 157, 145);
	font-family: 'Poppins', sans-serif;
	font-weight: 600;
	font-size: 1rem;
	margin: 0px 20px 30px 20px;
	text-align: center;
	color: brown;
}

.input:focus {
	border-bottom: 3px solid rgb(241, 46, 11);
	outline: none;
}

.plus_btn {
	border: 0px solid rgb(238, 136, 123);
	background-color: rgb(247, 157, 145);

	box-shadow: 5px 5px 10px rgb(235, 46, 46);
	color: white;
	padding: 0px 13px;
	font-family: 'Poppins', sans-serif;
	font-weight: 600;
	font-size: 2rem;

	border-radius: 40px;
}

.plus_btn:active {
	box-shadow: 3px 3px 5px rgb(235, 46, 46);
	background-color: rgb(236, 113, 113);
}

.card {
	display: flex;
	width: 100%;
	height: 40px;
	flex-direction: row;
	padding: 0px 5px;
	border: 3px solid rgb(247, 157, 145);
	justify-content: space-between;
	margin: 5px 0px;
	border-radius: 50px;
}

.del_btn {
	justify-self: flex-start;
	align-self: center;
	border: 0px solid rgb(238, 136, 123);
	background-color: rgb(247, 157, 145);

	color: white;

	width: 30px;
	height: 30px;

	font-family: 'Poppins', sans-serif;
	font-weight: 600;
	font-size: 1rem;

	border-radius: 15px;
}

.del_btn:active {
	background-color: rgb(236, 113, 113);
}

.task {
	align-self: center;
	width: 250px;

	font-size: 1rem;
}

